// Helper function to convert month number to API format
function getMonthNameForApi(monthNum) {
  const months = ['jan', 'feb', 'mar', 'apr', 'may', 'jun', 
                  'jul', 'aug', 'sep', 'oct', 'nov', 'dec'];
  return months[monthNum - 1] || 'jan';
}

const useCostStore = create((set, get) => ({
  // View Type
  viewType: 'production',
  setViewType: (type) => {
    console.log(' Changing view type to:', type);
    set({ viewType: type });
  },

  // API Data
  apiData: [],
  apiLoading: false,
  apiError: null,
  
  // Date Range
  currentYear: 2025,
  monthRange: { from: 8, to: 8 }, // Default: Current month (August)
  currentPeriodMonth: 8, // Current highlighted/selected month
  
  // Month Range Setter
  setMonthRange: (from, to) => {
    console.log(' Setting month range:', from, 'to', to);
    set({ monthRange: { from, to } });
  },
  
  // Current Period Month Setter
  setCurrentPeriodMonth: (month) => {
    console.log(' Setting current period month to:', month);
    set({ currentPeriodMonth: month });
  },
  
  // Theme
  selectedTheme: 'ocean',
  setSelectedTheme: (theme) => {
    console.log(' Changing theme to:', theme);
    set({ selectedTheme: theme });
  },
  
  // Location
  selectedLocation: 'Ranjangaon',
  setSelectedLocation: (location) => {
    console.log(' Setting location to:', location);
    set({ selectedLocation: location });
  },
  
  // Fetch Cost Data
fetchCostData: async (fromMonth, toMonth, year, viewType) => {
  try {
    set({ apiLoading: true, apiError: null });

    const { selectedLocation } = get();
    const normalizedPlant =
  plantAliasMap[selectedLocation] || selectedLocation;

const plantcode = plantCodeMap[normalizedPlant];

    if (!plantcode) {
      throw new Error("Invalid plant selected");
    }

    const baseUrl =
      viewType === "production"
        ? "prod_plantwise"
        : "sale_plantwise";

    const url =
      `https://ktfrancesrv2.kalyanicorp.com/internal/${baseUrl}` +
      `?view=month` +
      `&plantcode=${plantcode}` +
      `&year=${year}` +
      `&from_month=${fromMonth}` +
      `&to_month=${toMonth}`;

    console.log(" API CALL:", url);

    const response = await fetch(url);
    const result = await response.json();

    if (result.status === "success") {
      set({
        apiData: result.data,
        apiLoading: false,
        monthRange: { from: fromMonth, to: toMonth },
      });
    } else {
      set({
        apiError: result.message || "API Error",
        apiLoading: false,
      });
    }
  } catch (err) {
    console.error(" API Error:", err);
    set({
      apiError: err.message,
      apiLoading: false,
    });
  }
},

  
  // Quick helpers for common date ranges
  setCurrentMonth: () => {
    const month = new Date().getMonth() + 1;
    set({ 
      monthRange: { from: month, to: month },
      currentPeriodMonth: month 
    });
    return month;
  },
  
  setLast12Months: () => {
    const currentMonth = new Date().getMonth() + 1;
    const from = Math.max(1, currentMonth - 11);
    set({ 
      monthRange: { from, to: currentMonth },
      currentPeriodMonth: currentMonth 
    });
    return { from, to: currentMonth };
  },
  
  setFullYear: () => {
    set({ 
      monthRange: { from: 1, to: 12 },
      currentPeriodMonth: 12 
    });
    return { from: 1, to: 12 };
  },

  // Power Unit Modal State
  showPowerUnitModal: false,
  setShowPowerUnitModal: (show) => set({ showPowerUnitModal: show }),
  
  // Power Unit Data
  costCenters: [],
powerEntries: [],
costCenterLoaded: false,
powerEntryLoaded: false,

fetchCostCentersOnce: async () => {
  const { costCenterLoaded } = get();
  if (costCenterLoaded) return;

  const response = await fetch('https://ktfrancesrv2.kalyanicorp.com/api/v1/collection/cost_center_master');
  const data = await response.json();

  set({
    costCenters: (data.objects || []).filter(o => o.applicable_for_power === 1),
    costCenterLoaded: true
  });
},

fetchPowerEntriesOnce: async () => {
  const { powerEntryLoaded } = get();
  if (powerEntryLoaded) return;

  const response = await fetch('https://ktfrancesrv2.kalyanicorp.com/internal/machine_entry');
  const data = await response.json();

  set({
    powerEntries: data.data || [],
    powerEntryLoaded: true
  });
},


}));

export { useCostStore };

