import React, { useState, useRef, useEffect } from "react";
import {
  Calendar,
  ChevronLeft,
  ChevronRight,
  GripVertical,
} from "lucide-react";
import { useCostStore } from "../store/costStore";

const MonthRangeSlider = ({ theme }) => {
  const { viewType, monthRange, currentYear, fetchCostData, apiLoading } =
    useCostStore();

  const [isDragging, setIsDragging] = useState(false);
  const [dragType, setDragType] = useState(null); // 'start', 'end', or 'range'
  const [hoveredMonth, setHoveredMonth] = useState(null);
  const [tempRange, setTempRange] = useState(null);
  const sliderRef = useRef(null);
  const dragStartPos = useRef(null);

  // Available months (1-12)
  const months = [
    { num: 1, name: "January", short: "Jan", abbr: "Jan" },
    { num: 2, name: "February", short: "Feb", abbr: "Feb" },
    { num: 3, name: "March", short: "Mar", abbr: "Mar" },
    { num: 4, name: "April", short: "Apr", abbr: "Apr" },
    { num: 5, name: "May", short: "May", abbr: "May" },
    { num: 6, name: "June", short: "Jun", abbr: "Jun" },
    { num: 7, name: "July", short: "Jul", abbr: "Jul" },
    { num: 8, name: "August", short: "Aug", abbr: "Aug" },
    { num: 9, name: "September", short: "Sep", abbr: "Sep" },
    { num: 10, name: "October", short: "Oct", abbr: "Oct" },
    { num: 11, name: "November", short: "Nov", abbr: "Nov" },
    { num: 12, name: "December", short: "Dec", abbr: "Dec" },
  ];

  // Get current month (default start)
  const currentMonth = new Date().getMonth() + 1;

  // Get month position from mouse event
  const getMonthFromPosition = (clientX) => {
    if (!sliderRef.current) return null;
    const rect = sliderRef.current.getBoundingClientRect();
    const x = clientX - rect.left;
    const monthWidth = rect.width / 12;
    const month = Math.ceil(x / monthWidth);
    return Math.max(1, Math.min(12, month));
  };

  // Handle month selection by clicking
  const handleMonthClick = (monthNum) => {
    if (apiLoading || isDragging) return;

    let newFrom = monthRange.from;
    let newTo = monthRange.to;

    // Smart selection logic
    if (monthNum < monthRange.from || monthNum > monthRange.to) {
      // Clicking outside range - start new selection
      newFrom = monthNum;
      newTo = monthNum;
    } else {
      // Clicking inside range
      if (monthRange.from === monthRange.to) {
        // Single month - expand to clicked month
        newFrom = Math.min(monthNum, monthRange.from);
        newTo = Math.max(monthNum, monthRange.from);
      } else {
        // Multiple months - toggle edge or reset
        if (monthNum === monthRange.from) {
          newFrom = monthNum + 1;
        } else if (monthNum === monthRange.to) {
          newTo = monthNum - 1;
        } else {
          // Clicked middle - reset to single month
          newFrom = monthNum;
          newTo = monthNum;
        }
      }
    }

    // Ensure valid range
    if (newFrom > newTo) {
      [newFrom, newTo] = [newTo, newFrom];
    }

    fetchCostData(newFrom, newTo, currentYear, viewType);
  };

  // Quick select buttons
  const quickSelects = [
    {
      label: "Current Month",
      from: currentMonth,
      to: currentMonth,
      icon: "",
    },
    {
      label: "Last 3M",
      from: Math.max(1, currentMonth - 2),
      to: currentMonth,
      icon: "",
    },
    {
      label: "Last 6M",
      from: Math.max(1, currentMonth - 5),
      to: currentMonth,
      icon: "",
    },
    { label: "YTD", from: 1, to: currentMonth, icon: "" },
    { label: "Full Year", from: 1, to: 12, icon: "" },
  ];

  const handleQuickSelect = (from, to) => {
    if (apiLoading) return;
    fetchCostData(from, to, currentYear, viewType);
  };

  // Navigate month by month
  const handleNavigate = (direction) => {
    if (apiLoading) return;

    const rangeSize = monthRange.to - monthRange.from;
    let newFrom = monthRange.from;
    let newTo = monthRange.to;

    if (direction === "prev") {
      if (newFrom > 1) {
        newFrom = newFrom - 1;
        newTo = newFrom + rangeSize;
      }
    } else {
      if (newTo < 12) {
        newTo = newTo + 1;
        newFrom = newTo - rangeSize;
      }
    }

    // Clamp values
    newFrom = Math.max(1, newFrom);
    newTo = Math.min(12, newTo);

    if (newFrom !== monthRange.from || newTo !== monthRange.to) {
      fetchCostData(newFrom, newTo, currentYear, viewType);
    }
  };

  // Enhanced drag handlers
  const handleMouseDown = (e, monthNum, type) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(true);
    setDragType(type);
    dragStartPos.current = { month: monthNum, clientX: e.clientX };
    setTempRange({ from: monthRange.from, to: monthRange.to });
  };

  const handleMouseMove = (e) => {
    if (!isDragging || !sliderRef.current) return;

    const month = getMonthFromPosition(e.clientX);
    if (!month) return;

    setHoveredMonth(month);

    let newFrom = monthRange.from;
    let newTo = monthRange.to;

    if (dragType === "start") {
      newFrom = Math.min(month, monthRange.to);
    } else if (dragType === "end") {
      newTo = Math.max(month, monthRange.from);
    } else if (dragType === "range") {
      // Dragging entire range
      const delta = month - dragStartPos.current.month;
      newFrom = Math.max(1, Math.min(12, monthRange.from + delta));
      newTo = Math.max(1, Math.min(12, monthRange.to + delta));

      // Adjust if hitting boundaries
      if (newFrom < 1) {
        newTo = newTo - newFrom + 1;
        newFrom = 1;
      }
      if (newTo > 12) {
        newFrom = newFrom - (newTo - 12);
        newTo = 12;
      }
    }

    if (newFrom !== tempRange.from || newTo !== tempRange.to) {
      setTempRange({ from: newFrom, to: newTo });
      useCostStore.setState({ monthRange: { from: newFrom, to: newTo } });
    }
  };

  const handleMouseUp = () => {
    if (isDragging) {
      setIsDragging(false);
      setDragType(null);
      setHoveredMonth(null);
      dragStartPos.current = null;
      // Make API call with final range
      fetchCostData(monthRange.from, monthRange.to, currentYear, viewType);
    }
  };

  const handleRangeMouseDown = (e) => {
    const month = getMonthFromPosition(e.clientX);
    if (month >= monthRange.from && month <= monthRange.to) {
      handleMouseDown(e, month, "range");
    }
  };

  useEffect(() => {
    if (isDragging) {
      document.addEventListener("mousemove", handleMouseMove);
      document.addEventListener("mouseup", handleMouseUp);
      return () => {
        document.removeEventListener("mousemove", handleMouseMove);
        document.removeEventListener("mouseup", handleMouseUp);
      };
    }
  }, [isDragging, monthRange, tempRange]);

  // Get range display text
  const getRangeText = () => {
    if (monthRange.from === monthRange.to) {
      return `${months[monthRange.from - 1].name} ${currentYear}`;
    }
    return `${months[monthRange.from - 1].short} - ${
      months[monthRange.to - 1].short
    } ${currentYear}`;
  };

  return (
    <div
      className={`${theme.cardBg} rounded-2xl p-6 ${theme.shadow} ${theme.border} border mb-4 transition-all duration-300`}
    >
      {/* Header */}
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-3">
          <div
            className={`p-2 rounded-lg bg-gradient-to-br ${theme.accentGradient}`}
          >
            <Calendar className="w-5 h-5 text-white" />
          </div>
          <div>
            <h3 className={`text-base font-extrabold ${theme.primaryText}`}>
              Time Range Selector
            </h3>
            <p className={`text-sm font-semibold ${theme.mutedText}`}>{getRangeText()}</p>
          </div>
        </div>

        {/* Navigation Arrows */}
        <div className="flex items-center gap-2">
          <button
            onClick={() => handleNavigate("prev")}
            disabled={monthRange.from === 1 || apiLoading}
            className={`p-2 rounded-lg ${theme.buttonBg} ${theme.primaryText} hover:scale-105 hover:shadow-md disabled:opacity-30 disabled:cursor-not-allowed disabled:hover:scale-100 transition-all duration-200`}
            title="Previous period"
          >
            <ChevronLeft className="w-5 h-5" />
          </button>
          <button
            onClick={() => handleNavigate("next")}
            disabled={monthRange.to === 12 || apiLoading}
            className={`p-2 rounded-lg ${theme.buttonBg} ${theme.primaryText} hover:scale-105 hover:shadow-md disabled:opacity-30 disabled:cursor-not-allowed disabled:hover:scale-100 transition-all duration-200`}
            title="Next period"
          >
            <ChevronRight className="w-5 h-5" />
          </button>
        </div>
      </div>

      {/* Quick Select Buttons */}
      <div className="flex flex-wrap gap-2 mb-5">
        {quickSelects.map((select) => {
          const isActive =
            monthRange.from === select.from && monthRange.to === select.to;
          return (
            <button
              key={select.label}
              onClick={() => handleQuickSelect(select.from, select.to)}
              disabled={apiLoading}
              className={`group px-4 py-2 rounded-lg text-xs font-bold transition-all duration-200 ${
                isActive
                  ? `bg-gradient-to-r ${theme.accentGradient} text-white shadow-lg scale-105`
                  : `${theme.buttonBg} ${theme.primaryText} hover:scale-105 hover:shadow-md`
              } disabled:opacity-30 disabled:cursor-not-allowed disabled:hover:scale-100`}
            >
              <span className="mr-1">{select.icon}</span>
              {select.label}
            </button>
          );
        })}
      </div>

      {/* Enhanced Month Slider */}
      <div className="relative">
        {/* Drag tooltip */}
        {isDragging && hoveredMonth && (
          <div
            className="absolute -top-12 left-1/2 -translate-x-1/2 z-50 pointer-events-none"
            style={{
              left: `${((hoveredMonth - 0.5) / 12) * 100}%`,
            }}
          >
            <div
              className={`bg-gradient-to-r ${theme.accentGradient} text-white px-3 py-1.5 rounded-lg shadow-xl text-sm font-extrabold whitespace-nowrap`}
            >
              {dragType === "range"
                ? `${months[monthRange.from - 1].short} - ${
                    months[monthRange.to - 1].short
                  }`
                : months[hoveredMonth - 1].short}
              <div className="absolute left-1/2 -translate-x-1/2 -bottom-1 w-2 h-2 bg-inherit rotate-45" />
            </div>
          </div>
        )}

        <div
          ref={sliderRef}
          className={`relative h-20 bg-gradient-to-br from-gray-100 to-gray-50 rounded-xl overflow-hidden shadow-inner ${
            isDragging ? "cursor-grabbing" : "cursor-default"
          }`}
        >
          {/* Selected Range Background with Gradient */}
          <div
            onMouseDown={handleRangeMouseDown}
            className={`absolute top-0 bottom-0 bg-gradient-to-r ${
              theme.accentGradient
            } opacity-20 transition-all duration-300 ${
              !isDragging && monthRange.from !== monthRange.to
                ? "hover:opacity-30 cursor-grab"
                : ""
            } ${isDragging && dragType === "range" ? "opacity-30" : ""}`}
            style={{
              left: `${((monthRange.from - 1) / 12) * 100}%`,
              width: `${((monthRange.to - monthRange.from + 1) / 12) * 100}%`,
            }}
          >
            {/* Range drag indicator */}
            {!isDragging && monthRange.from !== monthRange.to && (
              <div className="absolute inset-0 flex items-center justify-center opacity-0 hover:opacity-100 transition-opacity">
                <GripVertical className={`w-6 h-6 ${theme.primaryText}`} />
              </div>
            )}
          </div>

          {/* Selected Range Border */}
          <div
            className={`absolute top-0 bottom-0 border-2 border-gradient-to-r ${theme.accentGradient} rounded-lg pointer-events-none transition-all duration-300`}
            style={{
              left: `${((monthRange.from - 1) / 12) * 100}%`,
              width: `${((monthRange.to - monthRange.from + 1) / 12) * 100}%`,
            }}
          />

          {/* Current Month Indicator */}
          <div
            className="absolute top-0 bottom-0 w-1 bg-blue-500 z-10 shadow-lg"
            style={{
              left: `${((currentMonth - 0.5) / 12) * 100}%`,
            }}
          >
            <div className="absolute -top-2 left-1/2 -translate-x-1/2 w-3 h-3 bg-blue-500 rounded-full shadow-md animate-pulse" />
            <div className="absolute -bottom-2 left-1/2 -translate-x-1/2 w-3 h-3 bg-blue-500 rounded-full shadow-md animate-pulse" />
          </div>

          {/* Month Markers */}
          <div className="absolute inset-0 flex">
            {months.map((month) => {
              const isInRange =
                month.num >= monthRange.from && month.num <= monthRange.to;
              const isStart = month.num === monthRange.from;
              const isEnd = month.num === monthRange.to;
              const isCurrent = month.num === currentMonth;
              const isHovered = month.num === hoveredMonth;

              return (
                <div
                  key={month.num}
                  onClick={() => handleMonthClick(month.num)}
                  className={`flex-1 flex flex-col items-center justify-center relative transition-all duration-200 ${
                    isInRange ? "bg-white/40" : ""
                  } ${
                    apiLoading
                      ? "cursor-not-allowed opacity-50"
                      : "hover:bg-white/60 cursor-pointer"
                  } ${isHovered && isDragging ? "bg-white/80" : ""}`}
                >
                  {/* Month Label */}
                  <div
                    className={`text-[11px] font-extrabold transition-all duration-200 ${
                      isInRange ? theme.primaryText : theme.mutedText
                    } ${isCurrent ? "text-blue-600 scale-110" : ""}`}
                  >
                    {month.abbr}
                  </div>

                  {/* Vertical divider */}
                  {month.num < 12 && (
                    <div className="absolute right-0 top-2 bottom-2 w-px bg-gray-200" />
                  )}
                </div>
              );
            })}
          </div>

          {/* Enhanced Drag Handles */}
          {/* Start Handle */}
          <div
            onMouseDown={(e) => handleMouseDown(e, monthRange.from, "start")}
            className={`absolute top-1/2 -translate-y-1/2 w-6 h-12 rounded-lg bg-gradient-to-r ${
              theme.accentGradient
            } cursor-ew-resize shadow-xl z-30 flex items-center justify-center group hover:scale-110 transition-all duration-200 ${
              isDragging && dragType === "start" ? "scale-125 shadow-2xl" : ""
            }`}
            style={{
              left: `calc(${((monthRange.from - 1) / 12) * 100}% - 12px)`,
            }}
          >
            <div className="flex flex-col gap-0.5">
              <div className="w-1 h-1 bg-white rounded-full opacity-80" />
              <div className="w-1 h-1 bg-white rounded-full opacity-80" />
              <div className="w-1 h-1 bg-white rounded-full opacity-80" />
            </div>
            {/* Pulse effect */}
            {isDragging && dragType === "start" && (
              <div className="absolute inset-0 rounded-lg bg-white animate-ping opacity-30" />
            )}
          </div>

          {/* End Handle */}
          <div
            onMouseDown={(e) => handleMouseDown(e, monthRange.to, "end")}
            className={`absolute top-1/2 -translate-y-1/2 w-6 h-12 rounded-lg bg-gradient-to-r ${
              theme.accentGradient
            } cursor-ew-resize shadow-xl z-30 flex items-center justify-center group hover:scale-110 transition-all duration-200 ${
              isDragging && dragType === "end" ? "scale-125 shadow-2xl" : ""
            }`}
            style={{
              left: `calc(${(monthRange.to / 12) * 100}% - 12px)`,
            }}
          >
            <div className="flex flex-col gap-0.5">
              <div className="w-1 h-1 bg-white rounded-full opacity-80" />
              <div className="w-1 h-1 bg-white rounded-full opacity-80" />
              <div className="w-1 h-1 bg-white rounded-full opacity-80" />
            </div>
            {/* Pulse effect */}
            {isDragging && dragType === "end" && (
              <div className="absolute inset-0 rounded-lg bg-white animate-ping opacity-30" />
            )}
          </div>
        </div>
      </div>

      {/* Info Bar */}
      <div className="mt-4 flex items-center justify-between text-xs">
        <div className={`flex flex-wrap items-center gap-3 ${theme.mutedText}`}>
          <div className="flex items-center gap-1.5">
            <div className="w-2.5 h-2.5 bg-blue-500 rounded-full animate-pulse" />
            <span className="font-bold">Current</span>
          </div>
          <div className="flex items-center gap-1.5">
            <div
              className={`w-2.5 h-2.5 bg-gradient-to-r ${theme.accentGradient} rounded-full`}
            />
            <span className="font-bold">Selected</span>
          </div>
          <div className="flex items-center gap-1.5">
            <GripVertical className="w-3 h-3" />
            <span className="font-bold">Drag to adjust</span>
          </div>
        </div>

        {apiLoading && (
          <div className="flex items-center gap-2 text-blue-600 font-bold">
            <div className="w-3 h-3 border-2 border-blue-600 border-t-transparent rounded-full animate-spin" />
            <span>Loading...</span>
          </div>
        )}
      </div>

      {/* Enhanced Selected Info Card */}
      <div
        className={`mt-4 p-4 rounded-xl bg-gradient-to-br ${theme.buttonBg} border ${theme.border} shadow-sm`}
      >
        <div className="flex items-center justify-between">
          <div>
            <span className={`font-extrabold text-sm ${theme.primaryText}`}>
              {monthRange.to - monthRange.from + 1} Month
              {monthRange.to - monthRange.from !== 0 ? "s" : ""} Selected
            </span>
            <span className={`${theme.mutedText} text-xs font-bold ml-2`}>
               {viewType === "production" ? " Production" : " Sales"} Data
            </span>
          </div>
          <div
            className={`px-3 py-1.5 rounded-lg bg-gradient-to-r ${theme.accentGradient} text-white text-xs font-extrabold shadow-md`}
          >
            {getRangeText()}
          </div>
        </div>
      </div>
    </div>
  );
};

export default MonthRangeSlider;