import React, { useState, useEffect } from "react";
import {
  Save,
  Loader2,
  AlertCircle,
  CheckCircle2,
  Plus,
  Trash2,
  X,
} from "lucide-react";
import { useCostStore } from "../store/costStore";
import { useMemo } from "react";
import { getAuthHeadersWithCSRF } from "../utils/authFetch";


export default function MachinePowerForm({ theme, onClose }) {
  //   const [costCenters, setCostCenters] = useState([]);
  //   const [entries, setEntries] = useState([]);
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [message, setMessage] = useState({ type: "", text: "" });
  const [selectedPlant, setSelectedPlant] = useState("");
  const {
    costCenters,
    powerEntries,
    fetchCostCentersOnce,
    fetchPowerEntriesOnce,
  } = useCostStore();

  useEffect(() => {
    fetchCostCentersOnce();
    fetchPowerEntriesOnce();
  }, []);

  useEffect(() => {
    if (costCenters.length > 0) {
      setLoading(false);
    }
  }, [costCenters]);

  useEffect(() => {
    setFormData(groupedData);
  }, [groupedData]);

  // Fetch cost center data
  //   useEffect(() => {
  //     fetchCostCenters();
  //     fetchPowerEntriesOnce();
  //   }, []);

  //   const fetchCostCenters = async () => {
  //     try {
  //       const response = await fetch('https://ktfrancesrv2.kalyanicorp.com/api/v1/collection/cost_center_master');
  //       const data = await response.json();

  //       if (data.objects) {
  //         const filtered = data.objects.filter(obj => obj.applicable_for_power === 1);
  //         setCostCenters(filtered);
  //       }
  //       setLoading(false);
  //     } catch (error) {
  //       console.error('Error fetching cost centers:', error);
  //       setMessage({ type: 'error', text: 'Failed to load cost center data' });
  //       setLoading(false);
  //     }
  //   };

  //   const fetchExistingEntries = async () => {
  //     try {
  //       const response = await fetch('https://ktfrancesrv2.kalyanicorp.com/internal/machine_entry');
  //       const data = await response.json();

  //       if (data.status === 'success' && data.data) {
  //         setEntries(data.data);
  //       }
  //     } catch (error) {
  //       console.error('Error fetching entries:', error);
  //     }
  //   };

  // Get unique plants
  const plants = [...new Set(costCenters.map((cc) => cc.plant_code))];

  // Filter cost centers by selected plant
  const filteredCostCenters = selectedPlant
    ? costCenters.filter((cc) => cc.plant_code === selectedPlant)
    : costCenters;

  const groupedData = useMemo(() => {
    return filteredCostCenters.reduce((acc, cc) => {
      const key = `${cc.line_name}|${cc.machine}`;

      if (!acc[key]) {
        const existing = powerEntries.find(
          (e) =>
            e.line_name === cc.line_name &&
            e.machine_name === cc.machine &&
            e.plant_code === parseInt(cc.plant_code)
        );

        acc[key] = {
          line_name: cc.line_name,
          machine: cc.machine,
          plant_code: cc.plant_code,
          powerUnit: existing ? existing.param_value : "",
          cdb_object_id: existing ? existing.cdb_object_id : null,
        };
      }
      return acc;
    }, {});
  }, [filteredCostCenters, powerEntries]);

  //   const [formData, setFormData] = useState(groupedData);
  const [formData, setFormData] = useState({});

  // Update form data when filters change
  useEffect(() => {
    const newGroupedData = filteredCostCenters.reduce((acc, cc) => {
      const key = `${cc.line_name}|${cc.machine}`;
      if (!acc[key]) {
        const existing = powerEntries.find(
          //  FIX
          (e) =>
            e.line_name === cc.line_name &&
            e.machine_name === cc.machine &&
            e.plant_code === parseInt(cc.plant_code)
        );

        acc[key] = {
          line_name: cc.line_name,
          machine: cc.machine,
          plant_code: cc.plant_code,
          powerUnit: existing ? existing.param_value : "",
          cdb_object_id: existing ? existing.cdb_object_id : null,
        };
      }
      return acc;
    }, {});
    setFormData(newGroupedData);
  }, [filteredCostCenters, powerEntries]); //  entries hata diya

  const handleInputChange = (key, value) => {
    setFormData((prev) => ({
      ...prev,
      [key]: {
        ...prev[key],
        powerUnit: value,
      },
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSubmitting(true);
    setMessage({ type: "", text: "" });

    let successCount = 0;
    let errorCount = 0;

    for (const [key, data] of Object.entries(formData)) {
      if (data.powerUnit && data.powerUnit.trim() !== "") {
        try {
          const payload = {
            plant_code: parseInt(data.plant_code),
            line_name: data.line_name,
            machine_name: data.machine,
            type: "POWER",
            param_value: parseFloat(data.powerUnit),
            entry_date: new Date().toISOString().split("T")[0],
          };

          const method = data.cdb_object_id ? "PUT" : "POST";
          if (method === "PUT") {
            payload.cdb_object_id = data.cdb_object_id;
          }
          const authOptions = await getAuthHeadersWithCSRF(method);

          const response = await fetch(
            "https://ktfrancesrv2.kalyanicorp.com/internal/machine_entry",
            {
              method: method,
              headers: {
                "Content-Type": "application/json",
              },
              body: JSON.stringify(payload),
              ...authOptions,
            }
          );

          const result = await response.json();

          if (result.status === "success") {
            successCount++;
          } else {
            errorCount++;
          }
        } catch (error) {
          console.error("Error saving entry:", error);
          errorCount++;
        }
      }
    }

    setSubmitting(false);

    if (errorCount === 0 && successCount > 0) {
      setMessage({
        type: "success",
        text: `${successCount} entries saved successfully!`,
      });
      fetchPowerEntriesOnce();
    } else if (successCount > 0 && errorCount > 0) {
      setMessage({
        type: "warning",
        text: `${successCount} entries saved, ${errorCount} failed`,
      });
    } else if (errorCount > 0) {
      setMessage({
        type: "error",
        text: `Failed to save entries`,
      });
    }

    // Clear message after 3 seconds
    setTimeout(() => setMessage({ type: "", text: "" }), 3000);
  };

  const handleDelete = async (key) => {
    const entry = formData[key];
    if (!entry.cdb_object_id) return;

    if (!confirm("Are you sure you want to delete this entry?")) return;

    try {
      const authOptions = await getAuthHeadersWithCSRF("DELETE", false);

      const response = await fetch(
        `https://ktfrancesrv2.kalyanicorp.com/internal/machine_entry?id=${entry.cdb_object_id}`,
        { method: "DELETE", ...authOptions,}
      );

      const result = await response.json();

      if (result.status === "success") {
        setMessage({ type: "success", text: "Entry deleted successfully!" });
        fetchPowerEntriesOnce();
      } else {
        setMessage({ type: "error", text: "Failed to delete entry" });
      }
    } catch (error) {
      console.error("Error deleting entry:", error);
      setMessage({ type: "error", text: "Error deleting entry" });
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <Loader2 className="w-8 h-8 animate-spin mx-auto text-blue-600 mb-2" />
          <p className="text-gray-600">Loading data...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8 px-4">
      <div className="max-w-6xl mx-auto">
        <div className="bg-white rounded-lg shadow-md">
          {/* Header */}
          {/* Header */}
          <div className="sticky top-0 bg-gradient-to-r from-blue-600 to-blue-700 text-white p-4 rounded-t-xl z-10">
            <div className="flex items-center justify-between">
              <div>
                <h2 className="text-xl font-bold">Machine Power Unit Entry</h2>
                <p className="text-blue-100 text-sm mt-1">
                  Enter power consumption units
                </p>
              </div>
              {/*  YE RAHA CLOSE BUTTON! */}
              <button
                onClick={onClose} //  Ye modal close karega
                className="p-2 rounded-lg bg-white/20 hover:bg-white/30 transition-colors"
              >
                <X className="w-5 h-5" /> {/*  X icon */}
              </button>
            </div>
          </div>

          {/* Message Alert */}
          {message.text && (
            <div
              className={`mx-6 mt-4 p-4 rounded-lg flex items-center gap-2 ${
                message.type === "success"
                  ? "bg-green-50 text-green-800 border border-green-200"
                  : message.type === "error"
                  ? "bg-red-50 text-red-800 border border-red-200"
                  : "bg-yellow-50 text-yellow-800 border border-yellow-200"
              }`}
            >
              {message.type === "success" ? (
                <CheckCircle2 className="w-5 h-5" />
              ) : (
                <AlertCircle className="w-5 h-5" />
              )}
              <span>{message.text}</span>
            </div>
          )}

          {/* Filter Section */}
          <div className="p-6 border-b">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Filter by Plant
            </label>
            <select
              value={selectedPlant}
              onChange={(e) => setSelectedPlant(e.target.value)}
              className="w-full md:w-64 px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            >
              <option value="">All Plants</option>
              {plants.map((plant) => (
                <option key={plant} value={plant}>
                  Plant {plant}
                </option>
              ))}
            </select>
          </div>

          {/* Form */}
          <form onSubmit={handleSubmit} className="p-6">
            <div className="overflow-x-auto">
              <table className="w-full border-collapse">
                <thead>
                  <tr className="bg-gray-50">
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700 border">
                      Line Name
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700 border">
                      Machine Name
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700 border">
                      Plant Code
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700 border">
                      Power Unit
                    </th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700 border">
                      Action
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {Object.entries(formData).map(([key, data]) => (
                    <tr key={key} className="hover:bg-gray-50">
                      <td className="px-4 py-3 border text-sm text-gray-700">
                        {data.line_name}
                      </td>
                      <td className="px-4 py-3 border text-sm text-gray-700">
                        {data.machine}
                      </td>
                      <td className="px-4 py-3 border text-sm text-gray-700">
                        {data.plant_code}
                      </td>
                      <td className="px-4 py-3 border">
                        <input
                          type="number"
                          step="0.01"
                          value={data.powerUnit}
                          onChange={(e) =>
                            handleInputChange(key, e.target.value)
                          }
                          placeholder="Enter power unit"
                          className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                        />
                      </td>
                      <td className="px-4 py-3 border">
                        {data.cdb_object_id && (
                          <button
                            type="button"
                            onClick={() => handleDelete(key)}
                            className="text-red-600 hover:text-red-800"
                            title="Delete entry"
                          >
                            <Trash2 className="w-4 h-4" />
                          </button>
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>

            {Object.keys(formData).length === 0 && (
              <div className="text-center py-8 text-gray-500">
                No machines found for power entry
              </div>
            )}

            {/* Submit Button */}
            <div className="mt-6 flex justify-end">
              <button
                type="submit"
                disabled={submitting}
                className="flex items-center gap-2 bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors font-medium"
              >
                {submitting ? (
                  <>
                    <Loader2 className="w-5 h-5 animate-spin" />
                    Saving...
                  </>
                ) : (
                  <>
                    <Save className="w-5 h-5" />
                    Save All Entries
                  </>
                )}
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}
